<template>
<!-- 歌单页面 -->
  <div class="songlistIndex">
   
    <transition name="fade">
      <Loading v-if="ishow"></Loading>
    </transition>
    
      <div class="nav">
        <AppHeader>精选歌单</AppHeader>
      <div class="nav-wrapper" ref="nav">
        <ul  ref="navlist">
          <li
            v-for="item in navlist"
            :key="item.id"
            :class="{active:currentIndex===item.id}"
            @click="changeCate(item)"
          >{{item.name}}</li>
        </ul>
      </div>
    </div>

      <Scroll ref="scroll" :pullup='pullup' :top='120' @pulldown="loadData">
    <div class="list">
        <ul class="songlist" ref="content">
          <li
            v-for="(item,index) in songlist"
            :key="index"
            class="listitem"
            @click="ListDetail(item)"
          >
            <div class="imgbox">
              <img v-lazy="item.coverImgUrl" alt />
              <!-- 播放量 -->
              <p class="playcount">
                <i class="el-icon-caret-right"></i>
                {{item.playCount|number}}
              </p>
            </div>
            <p>{{item.name}}</p>
          </li>
          <div style="clear:both"></div>
        </ul>
     </div>
      </Scroll>
  
  </div>
</template>


<script>
import Scroll from '../common/Scroll'
import AppHeader from '../user/component/AppHeader'
import Loading from '../common/Loading'
export default {
  filters:{
        // 格式播放量
    number(count){
        if(count>1000){
           return (count/10000).toFixed(2)+'万'
        }else{
           return count
        }
    }
    },
  components:{
      AppHeader,
      Loading,
    Scroll
  },
  data() {
    return {
      navlist: [
        {
            id:0,
            name:'全部',
            
        },
        {
            id:5001,
            name:'华语',
           
        },
        {
          id:1,
            name:'流行',
            
        },
        {
          id:2,
            name:'摇滚',
            
        },
        {
          id:1001,
            name:'民谣',
           
        }
      ],
      songlist:[],
      currentIndex: 0,
      pullup:true,
     listwidth:100,
     ishow:true
    };
  },
 async created(){
      // 获取全部歌单，默认全部
      const {data:res}=await this.$http.get('/top/playlist')
       this.songlist=res.playlists
       this.ishow=false
    //  console.log(this.navlist)
     this.loadData()
  
  },
  mounted(){
   this.$nextTick(()=>{
     this.loadData()
   })
  },
 activated(){
   this.loadData()
 },
  methods: {
  loadData(){
    this.$nextTick(()=>{
      this.$refs.scroll.scroll.refresh()
    })
  },

    // 根据标签分类歌单
   async changeCate(e) {
     console.log(e)
      this.currentIndex = e.id;
     this.ishow=true
        const {data:res}=await this.$http.get('/top/playlist',{
           params:{cat:e.name}
       })
       this.songlist=res.playlists
       this.ishow=false
    },
    ListDetail(e){
      // 获取当前歌单的id ,通过歌单信息获取详情，跳转详情页
      // console.log(e)
      this.listId=e.id
      this.$router.push({path:'/listdetail',query:{
        id:e.id,
        }})
    },
    }
 
}
</script>


<style lang='less' scoped>
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.songlistIndex {
  overflow: hidden;
  // height: 100%;
  // overflow-y: hidden;
}
// .el-scrollbar{
//   height: 100%;
//   overflow: auto;
// }
.nav {
  position: fixed;
  z-index: 99;
  width: 100%;
}


.nav-wrapper {
  background-color: #fff;
  height: 40px;
  border-bottom: 1px solid #eee;
  touch-action: none;
 
  ul{
    display: flex;
  justify-content: space-around;
  }
 li {
     overflow: hidden;
      float: left;
      // display: inline-block;
     width: 50px;
      height: 40px;
      line-height: 40px;
      text-align: center;

  }
  .active {
    font-weight: bold;
    border-bottom: 3px solid #de464e;
  }
}
.list {
  width: 100%;
  top: 120px;
  .songlist {
    width: 100%;
    .listitem {
      width: 100px;
      height: 170px;
      float: left;
      box-sizing: border-box;
      margin-left: 18px;
      .imgbox {
        width: 100%;
        height: 100px;
        position: relative;
        .playcount {
          position: absolute;
          top: 0;
          left: 0;
          color: #fff;
          img {
            width: 10px;
            height: 10px;
          }
        }
      }
      img {
        width: 100%;
        height: 100%;
      }
      p {
        width: 100%;
        font-size: 12px;
      }
    }
  }
}
</style>