<template>
  <div>
   
      <div class="nav" :style="{'background':'url('+coverImg+')'}">
          <i class="el-icon-back" @click="$router.go(-1)"></i>
          <p>歌单</p>
        </div>
         <Scroll ref="scroll" :top='0' @pulldown="loadData">
           <div class="scroll-wrapper">
      <!-- 上部分，歌单详情 -->
      <!-- 背景变暗 -->

      <div
        class="top-wrapper"
        :style="{'background':'linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('+coverImg+')'}"
      >
        
        <div class="top">
          <div class="imgbox">
            <img :src="list.coverImgUrl" alt />
            <!-- 播放量 -->
            <!--  -->
            <p class="playcount">
              <i class="el-icon-caret-right"></i>
              {{list.playCount|number}}
            </p>

            <!-- 作者 -->
            <div class="avatar">
              <div class="avatar-box">
                <img :src="avatarUrl" alt />
              </div>

              <p>{{nickname}}</p>
            </div>
          </div>
          <!-- 右侧信息 -->
          <div class="info">
            <p>{{list.name}}</p>

            <!-- 描述太长，设计一个文字展开和收缩 -->
            <p :class="['d-content',{'active':isDescStatus}]">{{list.description}}</p>
            <i class="el-icon-arrow-down more" @click="showMore($event)" v-if="!isShowMore"></i>
            <i class="el-icon-arrow-up" @click="showMore($event)" v-if="isShowMore"></i>
          </div>
        </div>
      </div>
      <div class="list-wrapper">
        <!-- 全部以及收藏菜单 -->
        <div class="menu">
          <p>
            <i class="el-icon-video-play"></i>
            <span>播放全部(共{{this.song.length}}首)</span>
          </p>
          <el-button round icon="el-icon-folder-add" size="medium">收藏({{this.list.trackCount}})</el-button>
        </div>
        <ul class="listbox">
          <li
            v-for="(item,index) in song"
            :key="index"
            class="songitem"
            @click="PlaySong(item ,index)"
          >
            <div class="left-text">
              <!-- 歌曲序号 -->
              <span class="num">{{index+1}}</span>
              <div :class="['songname',{'song-active':index===currentIndex}]">
                <!-- 歌曲名 -->
                <div>{{item.name}}</div>
                <!-- 歌曲信息 -->
                <div class="zj">{{item.al.name}}</div>
              </div>
            </div>

            <i class="el-icon-video-pause" v-if="index===currentIndex&&selected"></i>
          </li>
        </ul>
      </div>
      </div>
      </Scroll>
      <div class="bottom" :pullup='pullup' @click="songDetail">
        <div class="songimg">
          <img :src="isplay?currentsong.img:defaultsong.img" alt />
        </div>
        <div class="b-info">
          <p v-if="!isplay">{{this.defaultsong.name}}</p>
          <p v-if="isplay">{{this.currentsong.name}}</p>
        </div>
        <audio loop="loop" id="myAudio" ref="myAudio">
          <!-- <source :src="defaultsong.url" type="audio/mpeg" /> -->
        </audio>
        <i class="el-icon-video-pause" v-if="ishow" @click="showIcon"></i>
        <i class="el-icon-video-play" v-if="!ishow" @click="showIcon"></i>
      </div>
   
    
  </div>
</template>

<script>
import Scroll from "../common/Scroll";
export default {
  filters: {
    // 格式播放量
    number(count) {
      if (count > 1000) {
        return (count / 10000).toFixed(2) + "万";
      } else {
        return count;
      }
    },
  },
  components: {
    Scroll,
  },
  data() {
    return {
      id: "",
      list: [],
      coverImg: "",
      avatarUrl: "",
      nickname: "",
      isShowMore: false,
      isDescStatus: true,
      song: [],
      songId: [],
      currentIndex: "",
      isplay: false,
      ishow: false,
      selected: true,
      pullup:true,
      currentsong: {
        name: "",
        id: "",
        img: "",
        url: "",
      },
      defaultsong: {
        name: "春风十里",
        id: 38576323,
        img:
          "http://p3.music.126.net/hYjA4Th6CAdkWI_sm32juw==/109951163669459549.jpg",
        url: "https://music.163.com/song/media/outer/url?id=38576323.mp3",
      },
    };
  },
  async created() {
    //   当前歌单的id
    this.id = this.$route.query.id;
    const { data: res } = await this.$http.get("/playlist/detail", {
      params: { id: this.id },
    });
    // 获取歌单
    this.list = res.playlist;
    // 歌单封面
    this.coverImg = res.playlist.creator.backgroundUrl;
    // 头像图片
    this.avatarUrl = res.playlist.creator.avatarUrl;
    // 昵称
    this.nickname = res.playlist.creator.nickname;
    // 获取歌曲id
    res.privileges.forEach((item) => {
      this.songId.push(item.id);
    });
    // console.log(this.songId)
    // 歌曲详情
    this.getSongList();
    this.loadData()
  },
  activated(){
    this.loadData()
  },
  methods: {
    loadData(){
      this.$nextTick(()=>{
        this.$refs.scroll.scroll.refresh()
      })
    },
    //   展开详情
    showMore(e) {
      this.isShowMore = !this.isShowMore;
      this.isDescStatus = !this.isDescStatus;
    },
    // 歌单详情
    async getSongList() {
      const { data: res } = await this.$http.get(
        `/playlist/detail?id=${this.id}`
      );
      //   所有歌曲
      this.song = res.playlist.tracks;
    },
    // 点击播放歌曲，切换为选中歌曲
    async PlaySong(e, id) {
      this.currentsong.name = e.name;
      this.currentsong.id = e.id;
      this.currentsong.img = e.al.picUrl;
      this.currentIndex = id;
      this.isplay = true;
      //   const{data:res}=await this.$http.get('/song/url',{params:{id:e.id}})

      //   this.currentsong.url=res.data[0].url
      // 解决播放音乐403问题
      this.currentsong.url = `https://music.163.com/song/media/outer/url?id=${e.id}.mp3`;
      this.$refs.myAudio.src = this.currentsong.url;
      this.$refs.myAudio.play();
      this.ishow = true;
    },
    // 先判断是否优选中的歌曲
    showIcon() {
      // 切换播放按钮
      this.ishow = !this.ishow;
      //未选中播放歌曲，播放默认歌曲
      if (!this.isplay) {
        this.$refs.myAudio.src = this.defaultsong.url;
        this.$refs.myAudio.play();
        if (!this.ishow) {
          this.$refs.myAudio.pause();
        } else {
          this.$refs.myAudio.play();
        }
      } else {
        // 播放选中歌曲
        this.selected = true;
        this.$refs.myAudio.src = this.currentsong.url;
        if (!this.ishow) {
          this.$refs.myAudio.pause();
          this.selected = false;
        } else if (this.ishow) {
          this.$refs.myAudio.play();
        }
      }
    },
    // 跳转到歌曲歌词播放页
    songDetail(){
      if(this.currentsong.id){
        this.$router.push({path:'/play',query:{
        id:this.currentsong.id,
        img:this.currentsong.img,
        name:this.currentsong.name
      }})
      }else{
        this.$router.push({path:'/play',query:{
        id:this.defaultsong.id,
        img:this.defaultsong.img,
        name:this.defaultsong.name
      }})
      }
      
    }
  },
};
</script>

<style lang='less' scoped>
 .nav {
   width: 100vw;
    height: 50px;
    color: #fff;
    line-height: 50px;
  padding-left: 20px;
    position: fixed;
    z-index: 99;
    background-color: red;
    .el-icon-back {
      // display: inline-block;
      font-size: 20px;
      margin-right: 10px;
    }
    p {
      display: inline-block;
      font-size: 16px;
    }
  }
.top-wrapper {
  width: 100%;
  height: 260px;
  overflow: hidden;
  background-size: 100%;
  background-position: center top;
 

  .top {
    width: 96%;
    margin-left: 4%;
    height: 210px;
    display: flex;
    margin-top: 50px;
    .imgbox {
      width: 35%;
      position: relative;
      // height: 50%;
      img {
        width: 100%;
        // height: 100%;
      }
      .playcount {
        position: absolute;
        top: 0;
        right: 0;
        color: #fff;
        img {
          width: 10px;
          height: 10px;
        }
      }
    }
    .avatar {
      height: 40%;
      display: flex;
      font-size: 12px;
      color: #fff;
      .avatar-box {
        width: 45px;
        height: 45px;
        overflow: hidden;
        border-radius: 50%;
        justify-content: center;
        img {
          width: 100%;
          height: 100%;
        }
        p {
          padding-top: 20px;
        }
      }
    }
    .info {
      width: 60%;
      margin-left: 10px;
      color: #fff;

      .d-content {
        font-size: 10px;
      }
      .active {
        font-size: 10px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3; //超过三行折叠
        overflow: hidden;
      }
      .el-icon-arrow-down {
        float: right;
      }
      .el-icon-arrow-up {
        float: right;
      }
    }
  }
}
.menu {
  height: 40px;
  line-height: 40px;
  width: 100%;
  padding-top: 10px;
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  p {
    margin-left: 10px;
  }
  .el-button {
    background-color: #de464e;
    color: #fff;
    margin-right: 10px;
  }
}
.list-wrapper {
  position: relative;
  top: -23px;
  background-color: #fff;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px ;
 
  .listbox {
    width: 90%;
    margin-left: 5%;

    .songitem {
      height: 50px;
      // line-height: 60px;
      display: flex;
      justify-content: space-between;
      font-size: 14px;
      text-align: left;
      .left-text {
        display: flex;
        justify-content: end;
      }
      .num {
        display: inline-block;
        width: 20px;
        line-height: 50px;
        font-size: 14px;
        color: #999;
      }
      .el-icon-video-pause {
        line-height: 50px;
      }
      .songname {

        :first-child {
         overflow: hidden;
         height: 23px;
         width: 65%;
          padding-top: 5px;
          font-size: 14px;
        }
        :last-child {
          font-size: 6px;
          color: #999;
        }
      }
      .song-active {
        color: #de464e;
        :last-child {
          font-size: 6px;
          color: #de464e;
        }
      }
    }
  }
}
.bottom {
  width: 100%;
  height: 50px;
  line-height: 50px;
  border-top: 1px solid #eee;
  background-color: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 9;

  .songimg {
    //   display: inline-block;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    margin: 5px;
    float: left;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .b-info {
    float: left;
    line-height: 50px;
    :first-child {
      font-size: 12px;
      height: 20px;
    }
  }
  p {
    font-size: 12px;
  }
  .el-icon-video-pause,
  .el-icon-video-play {
    float: right;
    font-size: 24px;
    line-height: 50px;
    margin-right: 20px;
  }
}
</style>